<template>
    <div class="d-flex header align-items-center">
        <div v-if="!isMobile" class="col-10 d-flex align-items-center">
            <button class="botao" :class="{ active: activeSection === 'about-me' }"
                @click="scrollToSection('about-me')">
                <p :key="$i18n.locale">{{ $t('about') }}</p>
            </button>

            <div class="vertical-divider"></div>

            <button class="botao" :class="{ active: activeSection === 'projects' }"
                @click="scrollToSection('projects')">
                <p :key="$i18n.locale">{{ $t('projects') }}</p>
            </button>

            <div class="vertical-divider"></div>

            <button class="botao" :class="{ active: activeSection === 'services' }"
                @click="scrollToSection('services')">
                <p :key="$i18n.locale">{{ $t('services') }}</p>
            </button>

            <div class="vertical-divider"></div>

            <button class="botao" :class="{ active: activeSection === 'hire-me' }" @click="scrollToSection('hire-me')">
                <p :key="$i18n.locale">{{ $t('hireMe') }}</p>
            </button>
        </div>
        <div v-if="!isMobile" data-clickable class="col-1 ms-auto">
            <input type="checkbox" id="darkmode-toggle" style="cursor: none !important;" :checked="isDarkMode"
                @change="emitDarkMode" />
            <label for="darkmode-toggle" class="darkmode-toggle-label">
                <svg class="moon" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <g id="SVGRepo_bgCarrier" stroke-width="0"></g>
                    <g id="SVGRepo_tracerCarrier" stroke-linecap="round" stroke-linejoin="round"></g>
                    <g id="SVGRepo_iconCarrier">
                        <path
                            d="M21.0672 11.8568L20.4253 11.469L21.0672 11.8568ZM12.1432 2.93276L11.7553 2.29085V2.29085L12.1432 2.93276ZM7.37554 20.013C7.017 19.8056 6.5582 19.9281 6.3508 20.2866C6.14339 20.6452 6.26591 21.104 6.62446 21.3114L7.37554 20.013ZM2.68862 17.3755C2.89602 17.7341 3.35482 17.8566 3.71337 17.6492C4.07191 17.4418 4.19443 16.983 3.98703 16.6245L2.68862 17.3755ZM21.25 12C21.25 17.1086 17.1086 21.25 12 21.25V22.75C17.9371 22.75 22.75 17.9371 22.75 12H21.25ZM2.75 12C2.75 6.89137 6.89137 2.75 12 2.75V1.25C6.06294 1.25 1.25 6.06294 1.25 12H2.75ZM15.5 14.25C12.3244 14.25 9.75 11.6756 9.75 8.5H8.25C8.25 12.5041 11.4959 15.75 15.5 15.75V14.25ZM20.4253 11.469C19.4172 13.1373 17.5882 14.25 15.5 14.25V15.75C18.1349 15.75 20.4407 14.3439 21.7092 12.2447L20.4253 11.469ZM9.75 8.5C9.75 6.41182 10.8627 4.5828 12.531 3.57467L11.7553 2.29085C9.65609 3.5593 8.25 5.86509 8.25 8.5H9.75ZM12 2.75C11.9115 2.75 11.8077 2.71008 11.7324 2.63168C11.6686 2.56527 11.6538 2.50244 11.6503 2.47703C11.6461 2.44587 11.6482 2.35557 11.7553 2.29085L12.531 3.57467C13.0342 3.27065 13.196 2.71398 13.1368 2.27627C13.0754 1.82126 12.7166 1.25 12 1.25V2.75ZM21.7092 12.2447C21.6444 12.3518 21.5541 12.3539 21.523 12.3497C21.4976 12.3462 21.4347 12.3314 21.3683 12.2676C21.2899 12.1923 21.25 12.0885 21.25 12H22.75C22.75 11.2834 22.1787 10.9246 21.7237 10.8632C21.286 10.804 20.7293 10.9658 20.4253 11.469L21.7092 12.2447ZM12 21.25C10.3139 21.25 8.73533 20.7996 7.37554 20.013L6.62446 21.3114C8.2064 22.2265 10.0432 22.75 12 22.75V21.25ZM3.98703 16.6245C3.20043 15.2647 2.75 13.6861 2.75 12H1.25C1.25 13.9568 1.77351 15.7936 2.68862 17.3755L3.98703 16.6245Z"
                            :fill="isDarkMode ? '#2fcbd5' : '#bbb'"></path>
                    </g>
                </svg>
                <svg class="sun" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"
                    :stroke="isDarkMode ? '#555' : '#fff'">
                    <path d="M8 22H16" :stroke="isDarkMode ? '#555' : '#fff'" stroke-width="1.5" stroke-linecap="round"
                        stroke-linejoin="round"></path>
                    <path d="M5 19H19" :stroke="isDarkMode ? '#555' : '#fff'" stroke-width="1.5" stroke-linecap="round"
                        stroke-linejoin="round"></path>
                    <path d="M2 16H22" :stroke="isDarkMode ? '#555' : '#fff'" stroke-width="1.5" stroke-linecap="round"
                        stroke-linejoin="round"></path>
                    <path
                        d="M10 6.34141C10.6256 6.12031 11.2987 6 12 6C15.3137 6 18 8.68629 18 12C18 13.5217 17.4335 14.911 16.5 15.9687H7.5C6.56645 14.911 6 13.5217 6 12C6 11.2987 6.12031 10.6256 6.34141 10"
                        :stroke="isDarkMode ? '#555' : '#fff'" stroke-width="1.5" stroke-linecap="round"></path>
                </svg>
            </label>
        </div>
        <div class="col-1 d-flex flex-column d-md-block mt-auto mt-md-0 px-4 px-md-0">
            <div @click="toggleLanguage" class="language-toggle">
                <img data-clickable v-if="$i18n.locale === 'en'" src="../assets/usa_flag.png" alt="English"
                    class="language-icon" />
                <img data-clickable v-else src="../assets/br_flag.png" alt="Português" class="language-icon" />
                <p>{{ $t('language') }} <i class='fa fa-chevron-down'></i></p>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState, mapMutations } from 'vuex';

export default {
    data() {
        return {
            isMobile: window.innerWidth <= 768,
            showCertificates: false
        }
    },
    name: 'AppHeader',
    props: {
        isDarkMode: {
            type: Boolean,
            default: true,
        },
        ["currentSection"]: String
    },
    computed: {
        ...mapState(['currentView']),
        activeSection() {
            return this.currentSection;
        },
    },
    methods: {
        ...mapMutations(['setCurrentView']),
        updateIsMobile() {
            this.isMobile = window.innerWidth <= 768;
        },
        toggleLanguage() {
            if (this.$i18n.locale === 'en') {
                this.$i18n.locale = 'pt';
            } else {
                this.$i18n.locale = 'en';
            }
        },
        emitDarkMode(event) {
            this.$emit('toggle-dark-mode', event.target.checked);
        },
        scrollToSection(sectionId) {
            this.$emit("scroll-to-section", sectionId);
        },
        checkCurrentSection() {
            const sections = ["about-me", "projects", "services", "hire-me"];
            let current = "about-me";

            sections.forEach((id) => {
                const section = document.getElementById(id);
                if (section) {
                    const rect = section.getBoundingClientRect();
                    if (rect.top <= window.innerHeight / 3 && rect.bottom >= window.innerHeight / 3) {
                        current = id;
                    }
                }
            });

            this.$emit("update-section", current); // ✅ Emite evento para o App.vue
        }

    },
    mounted() {
        window.addEventListener("resize", this.updateIsMobile);
        window.addEventListener("scroll", this.checkCurrentSection);
    },
    beforeUnmounted() {
        window.removeEventListener("resize", this.updateIsMobile);
        window.removeEventListener("scroll", this.checkCurrentSection);
    },
};
</script>

<style>
@media (max-width: 768px) {
    .language-toggle{
    padding-left: 5vw;
}

}

.language-toggle p {
    display: flex;
    align-items: center;
    gap: 5px; /* Espaçamento entre o texto e o ícone */
}
</style>