<template>
  <div class="container-fluid">
    <div class="row">
      <!-- Header Section -->
      <!-- <h1 v-animate-on-scroll :key="$i18n.locale" class="text-center">{{ $t('welcome') }}</h1>  -->
      <div class="col-12 col-md-7">
        <div class="animated-text text-start mt-5 mb-3" :style="{ color: isDarkMode ? '#2fcbd5' : '#2275ff' }">
          <span>{{ currentWord }}</span>
        </div>

        <div class="col-auto">
          <p v-animate-on-scroll :key="$i18n.locale" style="margin-bottom: 6px;" class="text-start ">
            {{ $t('description') }}
          </p>
          <div class="custom-text-sequence">
            <p v-animate-on-scroll :key="$i18n.locale">
              {{ $t("it's about designing") }}
            </p>
            <p v-animate-on-scroll :key="$i18n.locale"
              :style="{ color: isDarkMode ? '#2fcbd5' : '#2275ff', fontWeight: 'bold' }">
              {{ $t('visual experiences') }}
            </p>
            <p v-animate-on-scroll :key="$i18n.locale">
              {{ $t('that tell stories') }}
            </p>
          </div>
        </div>


        <!-- Skills Section -->
        <div v-animate-on-scroll class="row justify-content-start align-items-start text-start mt-5">
          <div class="col-12 box3">
            <div class="box3 pt-5" style="margin-right: -3vw;">
              <h3 style="font-size: 2em;">{{ $t('skills') }}</h3>
              <div class="skills-chart d-flex justify-content-start">
                <!-- Usando v-if para destruir e recriar o gráfico sempre que isDarkMode mudar -->
                <BarChart v-if="barra == 0" :data="chartData" :options="chartOptions" />
              </div>
            </div>
          </div>
        </div>
      </div>


      <div v-animate-on-scroll style="margin-left:5vw;" class="col-12 col-md-4">

        <!-- About Section -->
        <div class="row justify-content-center align-items-center text-center"  style="margin-top: 15vh;">
          <div class="boxSkills col-md-12">
            <h3 :key="$i18n.locale" style="padding-top: 10px;" data-clickable @click="showAbout = !showAbout">
              {{ $t('about') }}
              <i :class="showAbout ? 'fa fa-chevron-up' : 'fa fa-chevron-down'" style="margin-left: 8px;"></i>
            </h3>
            <transition name="fade-slide" mode="out-in">

              <div v-if="showAbout" class="row" style="margin-top:20px;">

                <div class="col-12">
                  <p :key="$i18n.locale">{{ $t('myName') }}</p>
                  <p :key="$i18n.locale">{{ $t('actually') }}</p>
                  <p :key="$i18n.locale">{{ $t('aboutMe') }}</p>
                </div>
              </div>
            </transition>
          </div>

        </div>

        <!-- Soft Skills Section -->
        <div class="row justify-content-center align-items-center text-center">
          <div class="boxSkills col-md-12">
            <div>
              <h3 :key="$i18n.locale" style="padding-top: 10px;" data-clickable @click="softSkills = !softSkills">
                {{ softSkills ? $t('principal softskills') : $t('see softskills') }}
                <i :class="softSkills ? 'fa fa-chevron-up' : 'fa fa-chevron-down'" style="margin-left: 8px;"></i>
              </h3>
              <transition name="fade-slide" mode="out-in">
                <div v-if="softSkills" class="row" style="margin-top:20px;">
                  <div class="col-6 col-md-6 text-center tooltip-container"
                    :title="$t('emotional iteligence description')">
                    <img :src="isDarkMode ? require('@/assets/emotionalBlack.png') : require('@/assets/emotional.png')"
                      alt="Emotional Intelligence" class="softIcon" />
                    <p>{{ $t('emocional inteligence') }}</p>
                    <div class="tooltip">{{ $t('emotional inteligence description') }}</div>
                  </div>
                  <div class="col-6 col-md-6 text-center tooltip-container" :title="$t('communication description')">
                    <img
                      :src="isDarkMode ? require('@/assets/communicationBlack.png') : require('@/assets/communication.png')"
                      alt="Communication" class="softIcon" />
                    <p>{{ $t('communication') }}</p>
                    <div class="tooltip">{{ $t('communication description') }}</div>
                  </div>
                  <div class="col-6 col-md-6 text-center tooltip-container" :title="$t('teamwork description')">
                    <img :src="isDarkMode ? require('@/assets/teamworkBlack.png') : require('@/assets/teamwork.png')"
                      alt="Teamwork" class="softIcon" />
                    <p>{{ $t('teamwork') }}</p>
                    <div class="tooltip">{{ $t('teamwork description') }}</div>
                  </div>
                  <div class="col-6 col-md-6 text-center tooltip-container" :title="$t('pressure description')">
                    <img :src="isDarkMode ? require('@/assets/pressureBlack.png') : require('@/assets/pressure.png')"
                      alt="Pressure" class="softIcon" />
                    <p>{{ $t('pressure') }}</p>
                    <div class="tooltip">{{ $t('pressure description') }}</div>
                  </div>
                </div>
              </transition>
            </div>
          </div>
          <div class="col-6 mt-4">
            <a data-clickable class="mt-3 botaoDownload" :href="curriculoLink" download>{{
              $t('downloadResume') }}</a>
          </div>
        </div>
        
      </div>
    </div>
  </div>
</template>

<script>
import { Bar } from 'vue-chartjs';
import { Chart as ChartJS, Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale } from 'chart.js';

ChartJS.register(Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale);

export default {
  name: 'BarChartComponent',
  computed: {
    // Computa o link para o currículo baseado no idioma atual
    curriculoLink() {
      const idioma = this.$i18n.locale;
      return idioma === 'en' ? '/portfolio/resume.pdf' : '/portfolio/curriculo.pdf';
    }
  },
  props: {
    isDarkMode: Boolean
  },
  components: {
    BarChart: Bar
  },
  data() {
    return {
      showAbout: false,
      softSkills: false,
      branco: 'rgb(34, 133, 255)',
      brancoEscuro: 'rgba(34, 133, 255, 0.5)',
      azul: 'rgba(47, 203, 205, 0.6)',
      azulEscuro: 'rgb(47, 203, 205, 0.3)',
      barra: 0,
      skills: [
        { name: 'VueJs', level: 8 },
        { name: 'Laravel', level: 7 },
        { name: 'PHP', level: 5 },
        { name: 'CSS', level: 8 },
        { name: 'HTML', level: 7 },
        { name: 'Bootstrap', level: 7 },
        { name: 'Python', level: 6 },
        { name: 'Flutter', level: 5 },
        { name: 'English', level: 8 },
        { name: 'Git', level: 6 },
        { name: 'MySql', level: 5 },
        { name: 'Photoshop', level: 9 },
        { name: 'Illustrator', level: 6 },
      ],
      barThickness: 38, // valor inicial para telas maiores
      chartData: {
        labels: [],
        datasets: [
          {
            label: 'Skill level',
            data: [],
            backgroundColor: this.isDarkMode ? this.azulEscuro : this.brancoEscuro,
            borderColor: this.isDarkMode ? this.azul : this.branco,
            borderWidth: 1,
            barThickness: this.barThickness, // utiliza a variável de espessura
            categoryPercentage: 0.85,
            barPercentage: 1
          }
        ]
      },
      chartOptions: {
        elements: {
          bar: {
            borderWidth: 20,
          }
        },
        responsive: true,
        scales: {
          y: {
            beginAtZero: true,
            max: 10
          }
        }
      },
      words: ["Full Stack Developer", "Software Developer", "Graphic Designer", "Software Engineer", "Front-end Developer",],
      currentWord: "",
      currentIndex: 0,
      charIndex: 0,
      isDeleting: false,
      typingSpeed: 150, // Velocidade da digitação
    }
  },
  watch: {
    isDarkMode(newVal) {
      this.updateChartColors(newVal);
      this.barra = 1;
      setTimeout(() => {
        this.barra = 0;
      }, 10);
    }
  },
  methods: {
    handleIntersection(entry) {
      if (entry.isIntersecting) {
        console.log('Elemento entrou na viewport')
      } else {
        console.log('Elemento saiu da viewport')
      }
    }, animateText() {
      const typeEffect = () => {
        const word = this.words[this.currentIndex];

        if (!this.isDeleting) {
          this.currentWord = word.substring(0, this.charIndex + 1);
          this.charIndex++;

          if (this.charIndex === word.length) {
            this.isDeleting = true;
            setTimeout(typeEffect, 1000); // Pausa após completar a palavra
            return;
          }
        } else {
          this.currentWord = word.substring(0, this.charIndex - 1);
          this.charIndex--;

          if (this.charIndex === 0) {
            this.isDeleting = false;
            this.currentIndex = (this.currentIndex + 1) % this.words.length; // Avança para a próxima palavra
          }
        }

        setTimeout(typeEffect, this.isDeleting ? 50 : this.typingSpeed);
      };

      typeEffect();
    },

    updateChartColors(isDarkMode) {
      this.chartData.datasets[0].backgroundColor = isDarkMode ? this.azulEscuro : this.brancoEscuro;
      this.chartData.datasets[0].borderColor = isDarkMode ? this.azul : this.branco;
      this.chartData.datasets[0].borderWidth = 2;
    },
    adjustBarThickness() {
      if (window.innerWidth <= 768) {
        this.barThickness = 25; // reduz a espessura das barras para telas pequenas
      } else {
        this.barThickness = 38; // valor original para telas maiores
      }
      this.chartData.datasets[0].barThickness = this.barThickness;
    }
  },
  mounted() {
    this.animateText();
    this.adjustBarThickness(); // Ajusta a espessura das barras na montagem do componente
    window.addEventListener('resize', this.adjustBarThickness); // Ajusta ao redimensionar a tela
    this.chartData = {
      labels: this.skills.map(skill => skill.name),
      datasets: [
        {
          label: 'Skill level',
          backgroundColor: this.isDarkMode ? this.azulEscuro : this.brancoEscuro,
          borderColor: this.isDarkMode ? this.azul : this.branco,
          data: this.skills.map(skill => skill.level)
        }
      ]
    };
  },
  unmounted() {
    window.removeEventListener('resize', this.adjustBarThickness); // Remove o listener quando o componente for destruído
  }
};
</script>

<style scoped>
@media (max-width: 768px) {
  .col-md-4 {
    margin-left: 0 !important;
  }
}

.animated-text {
  font-size: 3em;
  font-weight: bold;
  color: #2fcbd5;
  display: inline-block;
  text-align: center;
  /* Centraliza o texto */
  width: 100%;
  /* Garante que ocupe toda a largura do contêiner */
  line-height: 1;
  /* Reduz o espaço entre as linhas */

}

@media (max-width: 768px) {

  /* Ajuste para dispositivos pequenos */
  .animated-text {
    min-height: 2.5em;
    /* Espaço maior para garantir duas linhas */
  }
}

.animated-text span {
  border-right: 3px solid #2fcbd5;
  padding-right: 5px;
  animation: blink 0.7s infinite;
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}


.skills-chart {
  height: 300px;
}

@media (max-width: 768px) {
  .skills-chart {
    height: 100%;
  }
}
</style>